.app {
    -webkit-backdrop-filter: blur(5px);
            backdrop-filter: blur(5px);
    background-color: rgba(0, 0, 0, 0.75);
}

.portfolio {
    flex-wrap: wrap;
}

.project {
    flex: 0 0 33%;
    border-radius: 10px;
}

.interactable { transition: -webkit-transform .1s; transition: transform .1s; transition: transform .1s, -webkit-transform .1s; }
.interactable:hover { -webkit-transform: scale(1.02); transform: scale(1.02); }
.interactable:active{ -webkit-transform: scale(0.99); transform: scale(0.99); }

.project-tech>p {
    margin: auto 0;
}

.noselect {
    -webkit-touch-callout: none; /* iOS Safari */
      -webkit-user-select: none; /* Safari */ /* Konqueror HTML */ /* Old versions of Firefox */
          -ms-user-select: none; /* Internet Explorer/Edge */
              user-select: none; /* Non-prefixed version, currently
                                    supported by Chrome, Edge, Opera and Firefox */
  }
